import React from 'react'
import one from '../images/1.gif'
import two from '../images/2.gif'
import three from '../images/3.gif'
import four from '../images/4.gif'
import five from '../images/5.gif'
import six from '../images/6.gif'
import seven from '../images/7.gif'
function Apeach() {

    return (
        <div >
            <div style={{ textAlign: "center" }}>
            <h1>Apeach 1:1 데이트 채팅 사이트</h1>
          
            <h2> 주요 기능</h2>
           
            <h3>●소켓활용 1:1채팅</h3>
            <h3>●회원가입시 코드를 발생하여 양 측 코드가 연결되어야 사용 가능</h3>
            <h3>●스케줄 작성 가능</h3>
            <h3>●갤러리 공유 가능</h3>
            </div>
       <hr  style={{border: '2px solid black'}}></hr>
        
            <div style={{ textAlign: "center" }}>
            <h3>1.회원가입 페이지 작동 화면</h3>
            <img src={one} style={{ height: '400px', width: '700px', border: '2px solid black', borderRadius: '15px' }} />
            <div>- 좌측 화면이 메인페이지 화면  </div>
            <div>- 회원가입 후 생성되는 초대 코드를 상대방과 연결</div>
            <h3>1-1)연결 실패 시</h3>
            <img src={two} style={{ height: '400px', width: '700px', border: '2px solid black', borderRadius: '15px' }} />
            <div>-매칭을 거절할 경우 매칭 실패 알람</div>
          
            <h3>1-2) 연결 성공 시</h3>
            <img src={three} style={{ height: '400px', width: '700px', border: '2px solid black', borderRadius: '15px' }} />
            <div>-개인 프로필 작성이 완료되면 로그인 페이지로 자동 이동</div>
            <h3>1-3)  로그인 후에 메인 페이지로 자동 이동</h3>
            <img src={four} style={{ height: '400px', width: '700px', border: '2px solid black', borderRadius: '15px' }} />
            <div>- 메인 페이지 이동</div>
            <div>- 본인 및 상대방 프로필 이미지 추가</div>
            <div>- 설정해둔 처음 만난 날로부터 날짜 계산 추가</div>
            <div>- 하단에 스케줄, 채팅, 갤러리 기능 추가 (좌측부터)</div>
            <div>- 본인 프로필 사진 누를 시 개인 프로필 페이지 이동</div>
            <h3>2.스케줄러 작동 화면</h3>
            <img src={five} style={{ height: '400px', width: '700px', border: '2px solid black', borderRadius: '15px' }} />
            <div>- Today 기능 추가</div>
            <div>- 좌측 상단 Add Event 으로 일정 추가</div>
            <h3>3.채팅 페이지 작동 화면</h3>
            <img src={six} style={{ height: '400px', width: '700px', border: '2px solid black', borderRadius: '15px' }} />
            <div>-채팅방 이름이 같아야만 입장이 가능</div>
            <div>- 상대방이 글을 적고 있으면 채팅창에 입력중 표기</div>
            <div>- 파일첨부 가능</div>
            <h3>4.갤러리 페이지 작동 화면</h3>
            <img src={seven} style={{ height: '400px', width: '700px', border: '2px solid black', borderRadius: '15px' }} />
            <div>- 파일 업로드 시 연결된 상대방의 앨범에도 공유</div>
    

            </div>
        </div>
    )
}

export default Apeach
