import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import './App.css';
import Apeach from './component/Apeach';
import IntroPage from './page/IntroPage';
import MainPage from './page/MainPage';
import WebPage from './page/WebPage';
import HomePage from './page/HomePage';
import U1L from './component/U1L';
function App() {
    return (
        <Router>
            <Switch>
                {/* <Route exact path="/portfolio" component={IntroPage} /> */}
                <Route exact path="/apeach" component={Apeach} />
                <Route exact path="/U1L" component={U1L} />
                <Route exact path="/main" component={MainPage} />
                <Route exact path="/Web" component={WebPage} />
                <Route exact path="/" component={MainPage} />
           
            </Switch>
        </Router>
    );
}

export default App;
